<template>
    <nacl-page-container no-header>
        <!-- <nacl-header transparent>编辑收货地址</nacl-header> -->
        <nacl-page-content>
            <form>
                <div class="row">
                    <label>收货人：</label>
                    <input
                        v-model="name"
                        type="text"
                    >
                </div>
                <div class="row">
                    <label>手机号码：</label>
                    <input
                        v-model="phone"
                        type="tel"
                    >
                </div>
                <div class="row">
                    <label>详细地址：</label>
                    <textarea
                        v-model="address"
                        class="address"
                    />
                </div>
            </form>
            <div class="btn-wrapper">
                <basic-button radius :loading="loading" @click="submit">保存</basic-button>
            </div>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import BasicButton from '../components/basic-button'
import REG_EXP from '../regexp'
import { getAddressDetails, saveAddress } from '../services'

export default {
    name: 'address-component',
    components: { BasicButton },
    data () {
        return {
            loading: false,
            name: '',
            phone: '',
            address: '',
            id: this.$route.query.id || null
        }
    },
    async mounted () {
        if (this.id !== '') {
            let data = await getAddressDetails(this.id)
            this.name = data.name
            this.phone = data.tel
            this.address = data.addressDetail
        }
        window.goRouting = this.goRouting;
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        async submit () {
            if (!this.name) {
                return this.$toast('请输入收货人姓名')
            }
            if (!this.phone || !REG_EXP.PHONE_EXP.test(this.phone)) {
                return this.$toast('请输入正确的手机号码')
            }
            if (!this.address) {
                return this.$toast('请输入收货地址')
            }
            this.loading = true
            await saveAddress({
                'id': this.id,
                'addressDetail': this.address,
                'areaCode': '1',
                'city': '1',
                'country': '1',
                'county': '1',
                'isDefault': false,
                'name': this.name,
                'postalCode': '1',
                'province': '1',
                'tel': this.phone
            })
            this.loading = false
            this.$toast('添加收货地址成功')
            // this.$router.history.go(-1)
            this.toGetUser();
            window.webkit.messageHandlers.goBack.postMessage('');
        }
    }
}
</script>

<style scoped lang="scss">
.title {
  font-size: 18px * 2;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  padding-top: 120px;
  text-align: center;
}
form {
  width: 341px * 2;
  margin: 32px auto 0 auto;
  padding: 10px 32px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}
.row {
  display: flex;
  padding: 14px * 2 0;
  border-top: 1px solid #ececec;
  font-size: 28px;
  &:first-child {
    border-top: 0;
  }
  label {
    width: 150px;
    text-align: justify;
    text-align-last: justify;
  }
  input {
    flex: 1 0;
  }
  .address {
    flex: 1 0;
    height: 74px * 2;
    font-size: 28px;
  }
}
.btn-wrapper {
  margin-top: 134px;
  padding: 0 142px;
}
</style>
