const PHONE_EXP = /^(((13[0-9])|(14[5-7])|(15[0-9])|(17[0-9])|(18[0-9])|(19[0-9]))+\d{8})$/
const PASSWORD_EXP = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,18}$/
const BANK_CODE_EXP = /^(\d{6,20})$/
const CHINESE_NAME_EXP = /^([\u4e00-\u9fa5]){2,7}$/
const CHINESE_ID_EXP = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/

const REG_EXP = {
    PHONE_EXP,
    PASSWORD_EXP,
    BANK_CODE_EXP,
    CHINESE_NAME_EXP,
    CHINESE_ID_EXP
}

export default REG_EXP
